import React from 'react';

import Progress from 'react-progressbar';

import { MCComponent } from '../../components';

import './index.scss';

import { ItemImage, getLevelInfomation } from './level';
import { CSSTransition } from 'react-transition-group';

import { Popup, FinishPopup, PopupDialog } from '../../components';

import { FacebookShareButton } from 'react-share';

import firebase from 'firebase';

function throttled(delay, fn) {
  let lastCall = 0;
  return function(...args) {
    const now = new Date().getTime();

    if (now - lastCall < delay) {
      lastCall = now;
      return;
    }
    lastCall = now;
    return fn(...args);
  };
}

const duration = 500;

class Point extends React.PureComponent {
  state = {
    in: false
  };
  componentDidMount() {
    this.setState({
      in: true
    });
  }
  render() {
    const { sign } = this.props;
    const signStr = sign > 0 ? `+1` : `-1`;
    return (
      <CSSTransition
        timeout={duration}
        in={this.state.in}
        classNames="point"
        // unmountOnExit
        onEntered={() => {
          console.log('onEntered');
          this.setState({ in: false });
        }}
      >
        <div
          className={`point ${sign > 0 ? 'increase-point' : 'decrease-point'}`}
        >{`${signStr}`}</div>
      </CSSTransition>
    );
  }
}
class ItemComp extends React.PureComponent {
  state = { active: false, signs: [] };

  constructor(props) {
    super(props);
    this._handleClick = throttled(500, this.handleClick);
  }
  handleClick = () => {
    const { targetId, itemId, layoutID, active, onChoose } = this.props;
    if (active) {
      console.log('>>>>>>>', itemId, targetId);
      if (itemId === targetId) {
        onChoose && onChoose(true, layoutID);
        this.setState(({ signs }) => ({ signs: [...signs, 1] }));
      } else {
        onChoose && onChoose(false, layoutID);
        this.setState(({ signs }) => ({ signs: [...signs, -1] }));
      }
    }
  };

  render() {
    const { signs } = this.state;
    const { layoutID, image, active } = this.props;

    const { className = '', hidden, activate } = this.props;
    return (
      <button
        className={`item ${className} ${(hidden || active || activate) &&
          'item-transparent'}`}
        id={layoutID}
        onClick={this._handleClick}
      >
        {!!active && <img className="image" src={image} />}
        {signs && signs.map((sign, index) => <Point sign={sign} key={index} />)}
      </button>
    );
  }
}

const PlayState = {
  PREVIEW: 1,
  RUNNING: 2,
  COMPLETED: 3,
  FINISH: 4
};

const PLAY_DURATION = 30;

const ITEM_TIME = 1500;

const BLOCK_TIME = 500;

const NEXT_TIME = 500;

class PlayPage extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      level: 1,
      time: PLAY_DURATION,
      point: 0,
      playState: PlayState.FINISH,
      miceActive: {}
    };
    this.blockItem = {};
    firebase
      .database()
      .ref('/topuser')
      .orderByChild('point')
      .limitToLast(2)
      .once('value')
      .then((snapshot) => {
        const topuser = Object.values(snapshot.val());
        console.log('topuser', snapshot.val());

        topuser &&
          this.setState({
            topuser: topuser
          });
      });
    if (window.mcd) {
      alert('window.mcd');
      document.addEventListener('mcdBridgeReady', function(e) {
        (function() {
          try {
            var user = window.mcd.bridge.message('user'); //access to user bridge
            user.send({ getuser: true }); //send command "getuser"
            user.on('data', function(data) {
              console.log('user data: ' + JSON.stringify(data));
            });
            user.on('error', function(error) {
              console.log('getuser error: ' + JSON.stringify(error));
              alert(error);
            });
            user.on('done', function(user) {
              //No more event will be emitted
              console.log('getuser done');
              alert(user);
            });
          } catch (error) {
            alert('error', error);
          }
        })();
      });
    } else {
      document.addEventListener('mcdBridgeReady', function(mcd) {
        try {
          var user = mcd.bridge.message('user'); //access to user bridge
          user.send({ getuser: true }); //send command "getuser"
          user.on('data', function(data) {
            console.log('user data: ' + JSON.stringify(data));
          });
          user.on('error', function(error) {
            console.log('getuser error: ' + JSON.stringify(error));
            alert(error);
          });
          user.on('done', function(user) {
            //No more event will be emitted
            console.log('getuser done');
            alert(user);
          });
        } catch (error) {
          alert('error', error);
        }
      });
    }
  }
  componentDidMount() {}
  start() {
    this.interval = setInterval(() => {
      this.setState(({ time }) => {
        if (time === 0) {
          if (this.interval) {
            clearInterval(this.interval);
            this.interval = undefined;
            this.completed();
          }
          return;
        }
        return { time: time - 1 };
      });
    }, 1000);
    this.playInterval = setInterval(() => {
      this.runShowRandom();
    }, NEXT_TIME);
  }
  stop() {
    if (this.interval) {
      clearInterval(this.interval);
      this.interval = undefined;
    }
    if (this.playInterval) {
      clearInterval(this.playInterval);
      this.playInterval = undefined;
    }
  }
  handleChoose = (isCorrect) => {
    console.log('handleChoose', isCorrect);
    this.setState(({ point }) => {
      if (isCorrect) {
        return {
          point: point + 1
        };
      } else if (point > 0) {
        return {
          point: point - 1
        };
      }
    });
  };
  completed() {
    this.setState(({ level }) => {
      if (level === 5) {
        return {
          playState: PlayState.FINISH,
          miceActive: {}
        };
      } else {
        return {
          playState: PlayState.COMPLETED,
          miceActive: {}
        };
      }
    });
    this.blockItem = {};
    this.stop();
  }
  renderLevelInfo() {
    const { level } = this.state;

    const { name, largeImage } = getLevelInfomation(level);

    return (
      <div className="level-info">
        <div className="popup-content">
          {`Trong vòng 30 giây hãy nhanh tay\nchọn đúng `}
          <span className="highlight">{name}</span>
          {` để được nhiều điểm nhất`}
        </div>
        <img className="image" src={largeImage} />
        <button
          className="page-button"
          onClick={() => {
            this.setState(
              {
                playState: PlayState.RUNNING,
                time: PLAY_DURATION
              },
              () => {
                this.start();
              }
            );
          }}
        >
          <span className="title">{'BẮT ĐẦU'}</span>
        </button>
      </div>
    );
  }
  renderCompletedInfo() {
    const { level, point } = this.state;
    const { largeImage } = getLevelInfomation(level);
    const nextLevel = level + 1;
    return (
      <div className="level-info">
        <div className="popup-content">
          {`Đã hoàn thành `}
          <span className="level">{`Vòng ${level}`}</span>
          {` và có `}
          <span className="highlight">{point}</span>
          {' điểm'}
        </div>
        <img className="image" src={largeImage} />
        <button
          className="page-button"
          onClick={() => {
            this.setState({
              level: nextLevel,
              playState: PlayState.PREVIEW
            });
          }}
        >
          <span className="title">{`TIẾP TỤC VÒNG ${nextLevel}`}</span>
        </button>
      </div>
    );
  }

  layoutArray(level) {
    if (!this.layoutMap) {
      this.layoutMap = {};
    }
    if (!this.layoutMap[level]) {
      const { layout } = getLevelInfomation(level);
      const arrays = [];
      layout.forEach((element) => {
        element.forEach(({ layoutID, hidden }) => {
          !hidden && arrays.push(layoutID);
        });
      });
      this.layoutMap[level] = arrays;
    }
    return this.layoutMap[level];
  }

  avaiableArray(level) {
    return this.layoutArray(level).filter(
      (layoutID) => this.blockItem[layoutID] === undefined
    );
  }

  runShowRandom() {
    const { level } = this.state;
    const { targetId } = getLevelInfomation(level);

    const array = this.avaiableArray(level);

    const numberOfItems = (Math.floor(Math.random() * 100) % 2) + 1;

    for (let index = 0; index < numberOfItems; index++) {
      var randLayoutId = array[Math.floor(Math.random() * array.length)];

      let itemId;
      if (!Object.values(this.blockItem).find((e) => e === targetId)) {
        itemId = targetId;
      } else {
        const itemIds = Object.keys(ItemImage);
        itemId = itemIds[Math.floor(Math.random() * itemIds.length)];
      }

      this.showMice(randLayoutId, parseInt(itemId));
    }
  }

  showMice(layoutID, itemId) {
    const { miceActive } = this.state;

    this.blockItem[layoutID] = itemId;

    miceActive[layoutID] = {
      active: true,
      itemId,
      image: ItemImage[itemId]
    };
    this.setState({
      miceActive: { ...miceActive }
    });
    setTimeout(() => {
      this.hideMice(layoutID);
    }, ITEM_TIME);
  }
  hideMice(layoutID) {
    const { miceActive } = this.state;
    miceActive[layoutID] = {
      active: false
    };
    this.setState({
      miceActive: { ...miceActive }
    });
    setTimeout(() => {
      delete this.blockItem[layoutID];
    }, BLOCK_TIME);
  }

  renderPlayBoard() {
    const { time, level, miceActive, point } = this.state;

    const { layout, targetId, smallImage } = getLevelInfomation(level);
    return (
      <React.Fragment>
        <div className="point-wrapper">
          <span className="title">{point}</span>
        </div>
        <div className="status">
          <img src={smallImage} className="chicken" />
          <span className="text level">{`Vòng ${level}`}</span>
          <span className="text">{`${time} giây`}</span>
        </div>

        <Progress completed={((level - 1) / 5.0) * 100} />

        <div className="board">
          {layout.map((row) => (
            <div className="row">
              {row.map((item) => (
                <ItemComp
                  {...miceActive[item.layoutID] || {}}
                  {...item}
                  targetId={targetId}
                  onChoose={this.handleChoose}
                />
              ))}
            </div>
          ))}
        </div>
      </React.Fragment>
    );
  }
  renderFinish() {
    const { point } = this.state;

    let voucherImage;
    if (point <= 20) {
      voucherImage = require('./images/voucher_bigmac_10@3x.jpg');
    } else if (point <= 40) {
      voucherImage = require('./images/voucher_matchadaxay@3x.jpg');
    } else if (point <= 60) {
      voucherImage = require('./images/voucher_matchalatteda@3x.jpg');
    } else if (point <= 80) {
      voucherImage = require('./images/voucher_matchalattenong@3x.jpg');
    } else {
      voucherImage = require('./images/voucher_bigmac_20@3x.jpg');
    }

    return (
      <FinishPopup>
        <div className="finish-body">
          <div className="popup-content" />
          <span className="finish-title">{'CHÚC MỪNG BẠN'}</span>
          <span className="popup-title">
            Đã hoàn thành <span className="level">5 Vòng</span> và có tổng số
            điểm là
          </span>
          <div className="point-wrapper finish-point-wrapper">
            <span className="title">{point}</span>
          </div>
          <span className="popup-title">Phần thưởng của bạn có được</span>
          <div className="banner-wrapper">
            <img className="banner" src={voucherImage} />
          </div>
          <button
            className="page-button"
            onClick={() => {
              document.addEventListener('mcdBridgeReady', function(e) {
                (function() {
                  try {
                    var offerActivation = window.mcd.bridge.message(
                      'offerActivation'
                    );
                    offerActivation.send({
                      loyaltyId: 1012,
                      autoActivate: false,
                      rewardId: 14929
                    });
                    offerActivation.on('data', (data) => {
                      console.log(
                        'offerActivation data' + JSON.stringify(data)
                      );
                      alert('error', JSON.stringify(data));
                    });
                    offerActivation.on('error', (error) => {
                      alert('error', JSON.stringify(error));
                      console.log(
                        'offerActivation error' + JSON.stringify(error)
                      );
                    });
                    offerActivation.on('done', () => {
                      console.log('offerActivation done');
                      alert('offerActivation done');
                    });
                  } catch (error) {
                    alert('error', error);
                  }
                })();
              });
            }}
          >
            <span className="title">{`Nhận quà`}</span>
          </button>
          <FacebookShareButton
            url={'https://www.mcdonalds.com/us/en-us.html'}
            className="facebook"
            beforeOnClick={() => {
              console.log('share share share');
            }}
            disabledStyle={true}
          >
            <img
              className="icon"
              src={require('./images/ic-facebook@3x.png')}
            />
            <span className="title">{`Chia sẻ để thêm lượt`}</span>
          </FacebookShareButton>
        </div>
      </FinishPopup>
    );
  }
  renderLeaderboard() {
    const { topuser = [] } = this.state;
    return (
      <Popup title={'Top 5 người chơi'}>
        {topuser.map(({ displayName, point }, index) => (
          <div className="leader-board-row" key={index}>
            <span className="no">{index + 1}</span>
            <span className="display-name">{displayName}</span>
            <span className="leader-board-point">{`${point} Điểm`}</span>
          </div>
        ))}
        <div className="leader-board-bottom">
          <span className="leader-board-bottom-title">
            {`5 người chơi cao điểm nhất tính tới ngày 14/2/2019 sẽ nhận được 1 McDonald's Premium Passport.`}
          </span>
          <button
            onClick={() => {
              this.setState({
                showDialog: true
              });
            }}
          >
            <span className="more">{`Xem chi tiết`}</span>
          </button>
        </div>
      </Popup>
    );
  }
  renderContent() {
    const { playState, level } = this.state;
    switch (playState) {
      case PlayState.PREVIEW:
        return <Popup title={`Vòng ${level}`}>{this.renderLevelInfo()}</Popup>;
      case PlayState.RUNNING:
        return this.renderPlayBoard();
      case PlayState.COMPLETED:
        return (
          <Popup title={`CHÚC MỪNG BẠN`}>{this.renderCompletedInfo()}</Popup>
        );
      case PlayState.FINISH:
        return (
          <React.Fragment>
            {this.renderFinish()}
            {this.renderLeaderboard()}
          </React.Fragment>
        );
      default:
        break;
    }
  }
  renderPopupDialog() {
    return (
      <PopupDialog
        title="Chi tiết"
        onClosePress={() => {
          this.setState({
            showDialog: false
          });
        }}
      >
        <span className="dialog-content">
          Lần đầu tiên xuất hiện tại Việt Nam chỉ với 55 cuốn, người sở hữu
          <b>McDonald’s Premium Passport</b> sẽ được quyền thưởng thức{' '}
          <b>miễn phí hơn 20 sản phẩm</b> và dịch vụ đặc trưng nhất của
          McDonald’s trong 1 năm tại hệ thống nhà hàng McDonald’s toàn quốc.
        </span>
        <img
          className="dialog-image"
          src={require('./images/passport-1@3x.jpg')}
        />
        <img
          className="dialog-image"
          src={require('./images/passport-2@3x.jpg')}
        />
      </PopupDialog>
    );
  }

  render() {
    const { time, point, level, running, playState } = this.state;
    const { showDialog } = this.state;

    return (
      <MCComponent>
        {this.renderContent()}
        {showDialog && this.renderPopupDialog()}
      </MCComponent>
    );
  }
}

export default PlayPage;
