import React from 'react';
import './index.scss';

import { PopupDialog } from '../../components';

class HomePage extends React.PureComponent {
  state = {
    rulePopup: false
  };
  handleClosePress = () => {
    this.setState({
      rulePopup: false
    });
  };
  handleOpenPopup = () => {
    this.setState({
      rulePopup: true
    });
  };

  render() {
    const { giftPopup, rulePopup } = this.state;
    return (
      <div className="home-page">
        <button
          className="button"
          onClick={() => {
            const { history } = this.props;
            history.push('./play');
          }}
        >
          <span className="title">CHƠI GAME</span>
        </button>

        <button className="rules" onClick={this.handleOpenPopup}>
          <span className="title">Thể lệ chương trình</span>
        </button>
        <button
          className="rules"
          onClick={() => {
            this.setState({
              giftPopup: true
            });
          }}
        >
          <img
            style={{
              width: 24,
              height: 24,
              marginRight: 10
            }}
            src={require('./images/gift@3x.png')}
          />
          <span className="title">Giải thưởng</span>
        </button>
        <img
          className="firewall"
          src={require('./images/firewall@3x.png')}
          alt=""
        />
        {rulePopup && (
          <PopupDialog
            title="Thể lệ chương trình"
            onClosePress={() => {
              this.setState({
                rulePopup: false
              });
            }}
          >
            <span className="dialog-content-title">
              Khi tham gia chơi game này, bạn đã đồng ý với các điều kiện khi
              dùng ứng dụng McDonald’s.
              <br />
              <br />
              <b>1. Đối tượng và thời gian tham gia</b>
              <br />• Đối tượng: Tất cả khách hàng McDonald’s tại Hà Nội và
              TP.Hồ Chí Minh không phân biệt độ tuổi.
              <br />• Thời gian: 25/1/2019 đến hết 28/2/2019.
              <br /> • Mỗi tài khoản có thể chơi nhiều lần và lấy nhiều phần
              thưởng.
              <br />
              <br />
              <b>2. Cách thức tham gia</b>
              <br /> • Bước 1: Tải ứng dụng McDonald’s
              <br />• Bước 2: Tạo tài khoản trên ứng dụng.
              <br />• Bước 3: Chơi game.
              <br />
              <br />
              <b>3. Cách chơi game</b>
              <br /> Mỗi người chơi có 1 lượt chơi. Mỗi lượt chơi gồm 5 vòng,
              mỗi vòng kéo dài 30s. Các món ăn đặc trưng của McDonald’s sẽ xuất
              hiện ngẫu nhiên và biến mất ở các vị trí, chọn đúng món +1 điểm,
              chọn sai món -1 điểm.
            </span>
          </PopupDialog>
        )}

        {giftPopup && (
          <PopupDialog
            title="Giải thưởng"
            onClosePress={() => {
              this.setState({
                giftPopup: false
              });
            }}
          >
            <span className="dialog-content-title">
              <b>Giải thưởng sau khi kết thúc lượt chơi:</b>
              <br />
              <br />
              Là các voucher sử dụng dịch vụ tại McDonald’s.
              <br />
              <br />
              <b>
                Giải thưởng dành cho người chơi xuất sắc (tính đến ngày kết thúc
                game):
              </b>
              <br />
              <br />- Top 5 người chơi cao điểm nhất: McDonald’s Passport – thỏa
              thích sử dụng miễn phí hơn 20 món ăn và dịch vụ McDonald’s tại tất
              cả các nhà hàng McDonald’s tại Việt Nam trong vòng 1 năm.
            </span>
            <img
              className="dialog-content-image"
              src={require('./images/passport-1@3x.jpg')}
            />
            <div className="dialog-content-title dialog-content-gift-title">
              - Top 50 người chơi cao điểm tiếp theo: Loa nghe nhạc không dây
              CocaCola
            </div>
            <img
              className="dialog-content-image"
              src={require('./images/radio@3x.jpg')}
            />
            <div className="dialog-content-title dialog-content-gift-title">
              - Top 500 người chơi cao điểm tiếp theo: Ly sứ cao cấp Coke Mug
            </div>
            <img
              className="dialog-content-image"
              src={require('./images/coke-mug@3x.jpg')}
            />
          </PopupDialog>
        )}
      </div>
    );
  }
}

export default HomePage;
