import React from 'react';

import './index.scss';

class MCComponent extends React.PureComponent {
  render() {
    const { className = '', ...props } = this.props;

    return (
      <div {...props} className={`mc-wrapper ${className}`}>
        <img
          className="headerbackground"
          src={require('./images/background_playgame@3x.jpg')}
        />
        {this.props.children}
      </div>
    );
  }
}

const Popup = ({ title = 'Vòng 1', children }) => (
  <div className="popup-background">
    <div className="content">
      <div className="popup-header">
        <img
          className="header-image"
          src={require('./images/bg_round_1@3x.png')}
        />
        <span className="title">{title}</span>
      </div>
      {children}
    </div>
  </div>
);
const FinishPopup = ({ title = 'Vòng 1', children }) => (
  <div className="popup-background">
    <div className="content">
      <div className="popup-header popup-header-finish">
        <img
          className="header-image"
          src={require('./images/bg_finish_final@3x.png')}
        />
      </div>
      {children}
    </div>
  </div>
);

class PopupDialog extends React.PureComponent {
  componentDidMount() {}
  render() {
    const { title, children } = this.props;
    return (
      <div className="popup">
        <div className="popup_inner">
          <div className="header">
            <span className="title">{title}</span>
            <button
              className="icclose_page_black"
              onClick={this.props.onClosePress}
            >
              <img
                className="image"
                src={require('./images/cancel@3x.png')}
                alt=""
              />
            </button>
          </div>
          <div className="content">{children}</div>
        </div>
      </div>
    );
  }
}

export { MCComponent, Popup, FinishPopup, PopupDialog };
